body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
}

.home {
  background-color: white;
  display: flex;
  flex-direction: column;
}

.home__title {
  height: 400px;
  width: 90%;
  background-color: white;
  text-align: center;
  display: table;
  font-family: "Source Code Pro", monospace;
  margin-left: 5%;
  margin-right: 5%;
  padding: 0;
  border-bottom: 2px solid lightgrey;
}

.home__title > p > h1 {
  font-weight: 700;
  font-size: 60px;
}

.home__title > p {
  font-size: 24px;
  text-align: center;
  vertical-align: middle;
  display: table-cell;
}

.navbar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navbar__top {
  display: flex;
  background-color: white;
  justify-content: space-evenly;
  margin: 20px 0;
  padding: 20px 5%;
}

.navbar__social {
  font-family: "Source Code Pro", monospace;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
  padding: 0 5%;
  margin-bottom: 20px;
}

.navbar__button {
  margin: 0 30px;
  border: 2px solid green;
  width: 85px;
  text-align: center;
  padding: 10px 30px;
  font-family: "Source Code Pro", monospace;
  font-size: 18px;
  color: green;
  font-weight: 700;
}

.navbar__link {
  margin: 5px 20px;
  padding: 10px;
}

.navbar__social > a {
  color: black;
}

.navbar__social > a:visited {
  color: black;
}

.navbar__social > a:hover {
  color: black;
}

.about {
  width: 90%;
  margin: 0 5%;
  background-color: white;
  font-family: "Source Code Pro", monospace;
}

h2 {
  padding-bottom: 10px;
  font-size: 32px;
  text-decoration: underline;
}

p {
  font-size: 18px;
  line-height: 30px;
}

.navbar__text {
  font-style: italic;
}

